* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  width: 100%;
  /* background-color: rgb(42, 41, 40); */
  background-image: linear-gradient(300deg, #0f0c29, #302b63, #24243e);
  height: 750px;
  margin: auto;
  margin-top: 10px;
}
.header1 {
  text-align: left;
}
.head1 {
  font-size: 40px;
  color: white;
}
.head2 {
  font-size: 40px;
  color: white;
}
.head3 {
  margin-top: 20px;
  font-size: 25px;
  color: white;
}
.head4 {
  color: #7fffd4;
  margin-top: 30px;
  font-size: 25px;
}
.head5 button {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  width: 200px;
  padding: 15px;
  margin-top: 40px;
  cursor: pointer;
  border: none;
  font-size: 30px;
  border-radius: 15px;
  background-color: #272341;
  color: white;
}
.head5 button:hover {
  background-color: black;
}
.header2 > img {
  width: 300px;
  margin: auto;
  border-radius: 100%;
  border: 2px solid rgb(18, 126, 235);
  transform: 10s;
}
.header2 > img:hover {
    transform: scale(1.2);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 768px) {
  .header {
    display: grid;
    height: 780px;
  }
  .header1 {
    text-align: center;
  }
  .head1 {
    font-size: 25px;
  }
  .head2 {
    font-size: 30px;
  }
  .head5 button {
    width: 180px;
    padding: 15px;
    font-size: 25px;
  }
  .header2 > img {
    width: 240px;
  }
}
@media screen and (max-width: 485px) {
  .head2 h1 {
    font-size: 18px;
  }
  .header {
    width: 100%;
  }
}
@media screen and (max-width: 320px) {
  .header2 > img {
    width: 200px;
  }
  .header {
    padding-top: 56%;
  }
  .head1 {
    font-size: 18px;
  }
  .head2 {
    font-size: 19px;
  }
  .head3 {
    font-size: 17px;
  }
  .head4 {
    font-size: 16px;
  }
}
